import { getAuthenticatedUser } from "@/lib/auth";
import { prisma } from "@/lib/prisma";
import {
  BrowseTabClient,
  WalletTabClient,
  SessionsTabClient,
  ProfileTabClient,
  SellerDashboardTabClient,
  SellerAnalyticsTabClient,
  SellerEarningsTabClient,
} from "@/components/dashboard/ClientTabs";
import { Card, CardContent } from "@/components/ui/card";
import { EmptyState } from "@/components/ui/empty-state";
import { MdError } from "react-icons/md";

interface PageProps {
  params: Promise<{ experienceId: string }>;
  searchParams: Promise<{ tab?: string; mode?: string }>;
}

export default async function ExperiencePage({
  params,
  searchParams,
}: PageProps) {
  const { experienceId } = await params;
  const { tab = "browse", mode = "buyer" } = await searchParams;

  // Get authenticated user (layout already verified access)
  const auth = await getAuthenticatedUser();

  if (!auth) {
    return (
      <EmptyState
        icon={MdError}
        title="Authentication Required"
        description="Please access this page through your Whop community."
      />
    );
  }

  let { user } = auth;

  // Auto-create seller profile if switching to seller mode
  if (mode === "seller" && !user.sellerProfile) {
    await prisma.sellerProfile.create({
      data: {
        userId: user.id,
        hourlyRate: 50, // Default rate
        bio: null,
        tagline: null,
        isActive: true,
      },
    });
    // Refetch user with the new seller profile
    user = await prisma.user.findUniqueOrThrow({
      where: { id: user.id },
      include: { sellerProfile: true },
    });
  }

  const isSeller = mode === "seller" && user.sellerProfile;

  // Render based on tab and mode
  if (isSeller) {
    return renderSellerTab(tab, experienceId, user);
  }

  return renderBuyerTab(tab, experienceId, user);
}

async function renderBuyerTab(
  tab: string,
  experienceId: string,
  user: {
    id: string;
    name: string | null;
    avatar: string | null;
    bio: string | null;
    email: string | null;
    createdAt: Date;
    sellerProfile: {
      id: string;
      hourlyRate: number;
      bio: string | null;
      tagline: string | null;
      averageRating: number | null;
      totalSessionsCompleted: number;
      totalReviews: number;
    } | null;
  }
) {
  switch (tab) {
    case "browse": {
      // Fetch ALL users, include seller profile if they have one
      const allUsers = await prisma.user.findMany({
        where: {
          id: { not: user.id }, // Exclude current user
        },
        include: {
          sellerProfile: {
            select: {
              id: true,
              hourlyRate: true,
              bio: true,
              tagline: true,
              averageRating: true,
              totalSessionsCompleted: true,
              isVerified: true,
              isActive: true,
            },
          },
        },
        orderBy: { createdAt: "desc" },
      });

      const mappedMembers = allUsers.map((u) => ({
        id: u.sellerProfile?.id || u.id,
        userId: u.id,
        hourlyRate: u.sellerProfile?.hourlyRate || null,
        bio: u.sellerProfile?.bio || null,
        tagline: u.sellerProfile?.tagline || null,
        averageRating: u.sellerProfile?.averageRating || null,
        totalSessionsCompleted: u.sellerProfile?.totalSessionsCompleted || 0,
        isVerified: u.sellerProfile?.isVerified || false,
        hasSellerProfile: !!u.sellerProfile,
        user: { id: u.id, name: u.name, avatar: u.avatar },
      }));

      return (
        <BrowseTabClient
          experienceId={experienceId}
          members={mappedMembers}
        />
      );
    }

    case "wallet": {
      const balances = await prisma.walletBalance.findMany({
        where: { holderId: user.id },
        include: {
          seller: {
            select: {
              id: true,
              name: true,
              avatar: true,
              sellerProfile: {
                select: {
                  hourlyRate: true,
                  averageRating: true,
                },
              },
            },
          },
        },
      });

      const mappedBalances = balances.map((b: typeof balances[number]) => ({
        id: b.id,
        balanceUnits: b.balanceUnits,
        avgPurchasePricePerUnit: b.avgPurchasePricePerUnit,
        totalPaid: b.totalPaid,
        seller: b.seller,
      }));

      return (
        <WalletTabClient
          experienceId={experienceId}
          balances={mappedBalances}
        />
      );
    }

    case "offers": {
      const sessions = await prisma.session.findMany({
        where: {
          OR: [{ buyerId: user.id }, { sellerId: user.id }],
        },
        include: {
          buyer: {
            select: { id: true, name: true, avatar: true },
          },
          seller: {
            select: { id: true, name: true, avatar: true },
          },
        },
        orderBy: { createdAt: "desc" },
      });

      const mappedSessions = sessions.map((s: typeof sessions[number]) => ({
        id: s.id,
        units: s.units,
        topic: s.topic,
        scheduledAt: s.scheduledAt?.toISOString() || null,
        status: s.status,
        meetingUrl: s.meetingUrl,
        buyer: s.buyer,
        seller: s.seller,
      }));

      return (
        <SessionsTabClient
          experienceId={experienceId}
          sessions={mappedSessions}
          userId={user.id}
        />
      );
    }

    case "profile": {
      const stats = await prisma.walletBalance.aggregate({
        where: { holderId: user.id },
        _sum: { balanceUnits: true, totalPaid: true },
      });

      const sessionCount = await prisma.session.count({
        where: { buyerId: user.id },
      });

      // Compute seller total earnings if seller
      let sellerTotalEarnings = 0;
      if (user.sellerProfile) {
        const sellerEarnings = await prisma.payout.aggregate({
          where: { sellerId: user.id, status: "COMPLETED" },
          _sum: { amount: true },
        });
        sellerTotalEarnings = sellerEarnings._sum.amount || 0;
      }

      return (
        <ProfileTabClient
          experienceId={experienceId}
          user={{
            id: user.id,
            name: user.name,
            avatar: user.avatar,
            bio: user.sellerProfile?.bio || null,
            createdAt: user.createdAt.toISOString(),
            sellerProfile: user.sellerProfile
              ? {
                  id: user.sellerProfile.id,
                  hourlyRate: user.sellerProfile.hourlyRate,
                  tagline: user.sellerProfile.tagline,
                  averageRating: user.sellerProfile.averageRating,
                  totalSessionsCompleted:
                    user.sellerProfile.totalSessionsCompleted,
                  totalEarnings: sellerTotalEarnings,
                }
              : null,
          }}
          stats={{
            totalPurchased: stats._sum.balanceUnits || 0,
            totalSessionsAsBuyer: sessionCount,
            totalSpent: stats._sum.totalPaid || 0,
          }}
        />
      );
    }

    default:
      return (
        <EmptyState
          icon={MdError}
          title="Page Not Found"
          description="The page you're looking for doesn't exist."
        />
      );
  }
}

async function renderSellerTab(
  tab: string,
  experienceId: string,
  user: {
    id: string;
    name: string | null;
    avatar: string | null;
    bio: string | null;
    email: string | null;
    createdAt: Date;
    sellerProfile: {
      id: string;
      hourlyRate: number;
      bio: string | null;
      tagline: string | null;
      totalSessionsCompleted: number;
      averageRating: number | null;
      totalReviews: number;
    } | null;
  }
) {
  if (!user.sellerProfile) {
    return (
      <EmptyState
        icon={MdError}
        title="Seller Profile Required"
        description="You need to set up a seller profile first."
      />
    );
  }

  switch (tab) {
    case "dashboard": {
      const pendingRequests = await prisma.session.count({
        where: {
          sellerId: user.id,
          status: "REQUESTED",
        },
      });

      const recentSessions = await prisma.session.findMany({
        where: { sellerId: user.id },
        include: {
          buyer: {
            select: { id: true, name: true, avatar: true },
          },
        },
        orderBy: { createdAt: "desc" },
        take: 10,
      });

      // Calculate earnings from payouts
      const completedPayouts = await prisma.payout.aggregate({
        where: { sellerId: user.id, status: "COMPLETED" },
        _sum: { amount: true },
      });

      const pendingPayouts = await prisma.payout.aggregate({
        where: { sellerId: user.id, status: { in: ["PENDING", "PROCESSING"] } },
        _sum: { amount: true },
      });

      const mappedRecentSessions = recentSessions.map((s: typeof recentSessions[number]) => ({
        id: s.id,
        topic: s.topic,
        scheduledAt: s.scheduledAt?.toISOString() || null,
        status: s.status,
        buyer: s.buyer,
      }));

      return (
        <SellerDashboardTabClient
          experienceId={experienceId}
          stats={{
            totalEarnings: completedPayouts._sum.amount || 0,
            pendingEarnings: pendingPayouts._sum.amount || 0,
            totalSessions: user.sellerProfile.totalSessionsCompleted,
            pendingRequests,
            averageRating: user.sellerProfile.averageRating,
            totalReviews: user.sellerProfile.totalReviews,
            hourlyRate: user.sellerProfile.hourlyRate,
          }}
          recentSessions={mappedRecentSessions}
        />
      );
    }

    case "requests": {
      // TODO: Create RequestsTab component
      return (
        <div className="p-4 sm:p-6 md:p-8">
          <h1 className="text-2xl font-semibold text-foreground mb-4">
            Booking Requests
          </h1>
          <Card>
            <CardContent className="py-12 text-center">
              <p className="text-muted-foreground">Requests tab coming soon</p>
            </CardContent>
          </Card>
        </div>
      );
    }

    case "offers": {
      const sessions = await prisma.session.findMany({
        where: { sellerId: user.id },
        include: {
          buyer: {
            select: { id: true, name: true, avatar: true },
          },
          seller: {
            select: { id: true, name: true, avatar: true },
          },
        },
        orderBy: { createdAt: "desc" },
      });

      const mappedSellerSessions = sessions.map((s: typeof sessions[number]) => ({
        id: s.id,
        units: s.units,
        topic: s.topic,
        scheduledAt: s.scheduledAt?.toISOString() || null,
        status: s.status,
        meetingUrl: s.meetingUrl,
        buyer: s.buyer,
        seller: s.seller,
      }));

      return (
        <SessionsTabClient
          experienceId={experienceId}
          sessions={mappedSellerSessions}
          userId={user.id}
        />
      );
    }

    case "earnings": {
      return <SellerEarningsTabClient experienceId={experienceId} />;
    }

    case "analytics": {
      return <SellerAnalyticsTabClient experienceId={experienceId} />;
    }

    default:
      return (
        <EmptyState
          icon={MdError}
          title="Page Not Found"
          description="The page you're looking for doesn't exist."
        />
      );
  }
}
